body {
  background: #c2c2d6;
  user-select: none;
  cursor: default;

  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

div {
  display: block;
}

#calculator {
  /* display: flex; */
  /* text-align: center; */
  margin: 10vh;
  border: 2px solid #47476b;
  padding: 5px;
  background: black;
  width: 320px;
  position: relative;
}

#formula {
  min-height: 25px;
  font-family: digital;
  font-size: 20px;
  color: orange;
  text-align: right;
  vertical-align: text-top;
  line-height: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

#display {
  min-height: 30px;
  font-size: 29px;
  font-family: digital;
  color: white;
  text-align: right;
  line-height: 30px;

  overflow-wrap: break-word;
  width: 320px;
}

button {
  position: relative;
  height: 65px;
  width: 80px;
  color: white;
  outline: 1px solid black;
  border: none;
  background: #4d4d4d;
  font-family: Share Tech Mono, monospace;
  font-size: 20px;
  cursor: default;
}

button:hover {
  color: orange;
  outline: .05em solid grey;
  z-index: 3;
}

#clear {
  /* width: 160px; */
  background: rgb(189, 129, 18);
}

#delete {
  background: rgb(189, 129, 18);
}

#zero {
  width: 160px;
}

#multiply, #subtract, #add, #divide {
  background: rgb(102, 102, 102);
}

#equals {
  background: rgb(0, 68, 102); 
  position: absolute; 
  height: 130px; 
  bottom: 5px;
}